import React, { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Post from "../../models/posts";
import Card from "../../components/Card";
import Button from "../../components/Button";
import "./SinglePost.scss";

const SinglePost = () => {
  const [post, setPost] = useState<Post>();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const location = useLocation();
  const navigate = useNavigate();
  const path =
    location.pathname.split("/")[location.pathname.split("/").length - 1];

  useEffect(() => {
    getSinglePost(path);
  }, []);

  const getSinglePost = async (pathParams: string) => {
    setLoading(true);

    try {
      const res = await fetch(`http://localhost:5000/api/post/${pathParams}`);
      const data = await res.json();
      setPost(data);
    } catch (error) {
      setError(true);
      navigate("/404");
    } finally {
      setLoading(false);
    }
  };

  const deletePost = async () => {
    try {
      const res = await fetch(`http://localhost:5000/api/post/${path}`, {
        method: "DELETE",
      });
      if (res) {
        alert("  !");
        navigate("/");
      }
    } catch (error) {
      alert("  .     !");
    }
  };

  const formatDate = (date: string) => {
    const getDate = new Date(date);
    return (
      getDate.getFullYear() +
      " / " +
      (getDate.getMonth() + 1) +
      " / " +
      getDate.getDate()
    );
  };

  return (
    <Card pageName="SinglePost">
      <div className="section header">
        <div>
          
          <h1>{post && post.title}</h1>
          {loading && <div>"  "</div>}
          {error && <div>"   "</div>}
        </div>
      </div>
      <div className="section body">
        <div className="infos">
          <div className="info">
            
            <span className="item">{post && post.postNumber}</span>
          </div>
          <div className="info author">
            
            <span className="item">{post && post.author}</span>
          </div>
          <div className="info">
            
            <span className="item">{post && formatDate(post.editedAt)}</span>
          </div>
        </div>
        <div className="content">
           <br />
          <p>{post && post.content}</p>
        </div>
      </div>
      <div className="section">
        <Button options={{ linkTo: `/post/update/${path}` }}>
            
        </Button>
        <Button options={{ onClick: deletePost }}></Button>
        <Button options={{ linkTo: "/" }}></Button>
      </div>
    </Card>
  );
};

export default SinglePost;
