import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Button from "../../components/Button";
import Card from "../../components/Card";
import "./UpdatePost.scss";

const UpdatePost = () => {
  const [titleInput, setTitleInput] = useState("");
  const [contentInput, setContentInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const navigate = useNavigate();
  const path =
    location.pathname.split("/")[location.pathname.split("/").length - 1];

  useEffect(() => {
    getSinglePost(path);
  }, []);

  const getSinglePost = async (pathParams: string) => {
    setLoading(true);

    try {
      const res = await fetch(`http://localhost:5000/api/post/${pathParams}`);
      const data = await res.json();
      setTitleInput(data.title);
      setContentInput(data.content);
    } catch (error) {
      setError(true);
    } finally {
      setLoading(false);
    }
  };

  const requestOptions = {
    method: "PUT",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ title: titleInput, content: contentInput }),
  };

  const submitHandler = async (event: any) => {
    try {
      event.preventDefault();
      const res = await fetch(
        `http://localhost:5000/api/post/${path}`,
        requestOptions
      );
      const data = await res.json();

      if (res) alert(" !");
      navigate(`/post/${data.__id}`);
    } catch (error) {
      alert("  .    ");
    }
  };

  return (
    <form onSubmit={submitHandler}>
      <Card pageName="UpdatePost">
        <div className="section header">
          {!error && !loading && <h2> </h2>}
          {loading && <div>  </div>}
          {error && <div>   </div>}
        </div>
        <div className="section">
          <div>
            <label></label>
            <br />
            <input
              className="title"
              type="text"
              value={titleInput}
              onChange={(event) => setTitleInput(event.target.value)}
              placeholder=" !"
            />
          </div>
          <div>
            <label></label>
            <br />
            <textarea
              className="content"
              value={contentInput}
              onChange={(event) => setContentInput(event.target.value)}
              placeholder=" !"
            />
          </div>
        </div>
        <div className="section">
          <Button options={{ type: "submit" }}></Button>
          <Button options={{ linkTo: "/" }}></Button>
        </div>
      </Card>
    </form>
  );
};

export default UpdatePost;
